/** Estilos sobreescritos */
.g-cartografia-flecha-izquierda {
  color: #00796B !important;
}
.g-cartografia-flecha-derecha {
  color: white !important;
  background-color: rgba(8,112,31,.8)  !important;
}
#div-visor.m-control .m-visor-button-panel{
  color: #9E9E9E !important;
}
#div-visor.m-control.activated .m-visor-button-panel{
  color: rgba(8,112,31,.8)  !important;
}
#div-catastro.m-control .m-visor-button-panel img{
  filter: grayscale(100%) brightness(200%);
}
#div-catastro.m-control.activated .m-visor-button-panel img{
  filter: none;
}

/*.m-control.activated*/
.m-visor{
  user-select: none;
}

/** Estilos propios*/
.info{
  position : absolute;
  width : 60%;
  padding : 5px;
  top : 10px;
  left : 20%;
  background : rgba(245, 245, 245, 0.8);
  font-size : large;
  border-radius : 5px;
  border-style: dashed;
  border-width: 2px;
  text-align: center;
}
.m-visor #visor-title{
  text-align: center;
  font-size: 1.25em;
  padding: 2%;
  background-color: #f0f0f0;
  border-top: 1px solid rgba(0,0,0,.13);
  border-bottom: 1px solid rgba(0,0,0,.13);
}

.m-visor #catastro-title{
  text-align: center;
  font-size: 1.25em;
  padding: 2%;
  background-color: #f0f0f0;
  border-top: 1px solid rgba(0,0,0,.13);
  border-bottom: 1px solid rgba(0,0,0,.13);
}

.m-visor.collapsed {
  max-height: 0;
}
.m-visor.opened .m-panel-controls{
  width: 30vw;
  background-color: #f0f0f0;
  border-radius: 0 !important;
}
.m-visor #div-visor{
  width: 50%;
  text-align: center;
}
.m-visor #div-catastro{
  width: 50%;
  text-align: center;
}

.m-visor.opened #div-catastro.activated #div-visor-catastro{
  position: absolute;
  width: 30vw;
  right: 0;
  background-color: white;
  text-align: center;
}
.m-visor #div-catastro.activated #div-visor-catastro > *{
  margin: auto;
}

.m-visor.opened #div-visor.activated #div-visor-cartografia{
  position: absolute;
  width: 30vw;
  right: 0;
  background-color: white;
  text-align: center;
}
.m-visor #div-visor.activated #div-visor-cartografia > *{
  margin: auto;
  margin-bottom: 0%;
}

@media only screen and (max-width: 1200px){
  .m-visor.opened .m-panel-controls{
    width: 75vw;
  }
  .m-visor.opened #div-catastro.activated #div-visor-catastro{
    position: absolute;
    width: 75vw;
    background-color: white;
    text-align: center;
  }
  .m-visor #div-catastro.activated #div-visor-catastro > *{
    margin: auto;
  }

  .m-visor.opened #div-visor.activated #div-visor-cartografia{
    position: absolute;
    width: 75vw;
    background-color: white;
    text-align: center;
  }
  .m-visor #div-visor.activated #div-visor-cartografia > *{
    margin: auto;
  }
}

.m-visor #div-visor-cartografia{
  margin: auto;
}
.m-visor #m-visor-select{
  padding-bottom: 0%;
  padding-left: 0%;
  padding-right: 1%;
  margin: auto;
}
.m-visor .selectableToHide{
  list-style: none;
}
.m-visor .selectTitle{
  text-align: left;
  width: 99%;
  background-color: #f1f1f1;
  padding-left: 2%;
  padding: 1%;
  margin: 0;
  cursor: pointer;
  border-bottom: 2px solid rgb(210, 210, 210);
}
.m-visor .selectableGroup{
  padding-inline-start: 0;
  margin-left: 3%;
}
.m-visor .selectableToChangeLayer{
  list-style: none;
  text-align: left;
  cursor: pointer;
  width: 99%;
  background-color: rgb(255, 255, 255);
  padding-left: 2%;
  padding: 1%;
}

.m-visor #ctrlCartografia{
  font-size: 2em;
  padding: 4%;
}

.m-visor #ctrlCatastro{
  height: 95%;
  margin-top: 1%;
  color: rgba(250, 250, 250, 0.9);
}
.m-visor #div-visor.activated #ctrlCartografia{
  color: rgba(8,112,31,.8) ;
}
.m-visor #div-catastro .m-visor-button-panel{
  height: 40px;
}
.m-visor #div-visor .m-visor-button-panel{
  height: 40px;
}
.m-visor .layerSelected{
  background-color: rgba(8,112,31,.8)  !important;
  color: white;
}
.flatpickr-calendar{
  margin-top: 5% !important;
  margin-bottom: 5% !important;
}






/*==============================================
  FILTROS
*/
.m-visor #options-title-catastro{
  text-align: center;
  font-size: 1.25em;
  padding: 2%;
  background-color: #f0f0f0;
  border-top: 1px solid rgba(0,0,0,.13);
  border-bottom: 1px solid rgba(0,0,0,.13);
  cursor: pointer;
}
.m-visor #options-title-visor{
  text-align: center;
  font-size: 1.25em;
  padding: 2%;
  background-color: #f0f0f0;
  border-top: 1px solid rgba(0,0,0,.13);
  border-bottom: 1px solid rgba(0,0,0,.13);
  cursor: pointer;
}
.m-visor .option-input{
  width: 95%;
  margin-top: 2%;
  text-align: left;
  margin-inline-start: 2%;
}
.m-visor .option-input select{
  width: 100%;
}
.m-visor #div-options-catastro{
  padding-bottom: 5%;
  padding-top: 2%;
}
.m-visor #div-options-visor{
  padding-bottom: 5%;
  padding-top: 2%;
}

.m-visor .slider{
  height: 0.5em;
  -webkit-appearance: none;
  outline-width: 0;
  width: 100%;
  right: 0;
}

.m-visor label{
  width: 10%;
  margin-right: 5%;
}

.m-visor #filter{
  margin-top: 3%;
  margin-bottom: 3%;
}


